import Vue from 'vue';
import VuePlyr from 'vue-plyr';
import { generateFilePath } from '@nextcloud/router';
Vue.use(VuePlyr);
export default {
  name: 'Audios',
  computed: {
    player: function player() {
      return this.$refs.plyr.player;
    },
    options: function options() {
      return {
        controls: ['play-large', 'play', 'progress', 'current-time', 'mute', 'volume', 'settings'],
        iconUrl: generateFilePath('viewer', 'img', 'plyr.svg')
      };
    }
  },
  watch: {
    active: function active(val, old) {
      // the item was hidden before and is now the current view
      if (val === true && old === false) {
        this.player.play(); // the item was playing before and is now hidden
      } else if (val === false && old === true) {
        this.player.pause();
      }
    }
  },
  methods: {
    donePlaying: function donePlaying() {
      this.$refs.audio.autoplay = false;
      this.$refs.audio.load();
    }
  }
};